<template>
    <Transition name="modal">
       <div class="modal" @click="closeModal">
           <div class="modal__block" @click.stop="">
               <h2 class="modal__title">
                   {{edit ? 'Изменить цитату' : 'Добавить цитату'}}
               </h2>
               <div class="modal__inputs">
                   <label>
                       <span>Текст цитаты</span>
                       <input type="text" v-model="quoteText">   
                   </label>
                   <label>
                       <span>ФИО</span>
                       <input type="tel" v-model="quoteAuthor"
                       >
                   </label>
                   <label>
                       <span>Жанр</span>
                       <input type="text" v-model="quoteGenre" 
                       >
                   </label>
               </div>
               <div class="modal__btns">
                   <button 
                   class="modal__btn cancel" 
                   @click="closeModal"
                   >
                   Отмена
                  </button>
                   <button 
                   class="modal__btn add" 
                   @click="addquote"
                   v-if="!edit"
                   >
                   Добавить
                  </button>
                   <button 
                   class="modal__btn add" 
                   @click="changequote"
                   v-else
                   >
                   Изменить
                   </button>
               </div>
           </div>
       </div>
   </Transition>
   </template>
   
   <script>
   
   import { computed } from '@vue/reactivity';
   
       export default{
           data(){
             return {
                quoteText: '',
                quoteAuthor: '',
                quoteGenre: '',
                timeCreating: '',
                timeEditing: '',
                id: 1,
                condition: false,
                addedId: 1,
             }
           },
           props: {
               currentId: Number,
               edit: Boolean,
               editquote: Object,
               quotes: Array
           },
           methods: {
               closeModal(){
                   this.$emit('closeModal', this.condition)
                   this.quoteText = '';
                   this.quoteAuthor = '';
                   this.quoteGenre = '';
               },
              addquote(quotes){
                if(this.id <= this.quotes.length){
                this.addedId += this.quotes.length
            }
                if(this.name != '' && this.quoteAuthor != '' && this.quoteGenre != '' )
                {
                    let today = new Date();
                    let time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
                   const item = {
                   id: this.addedId,
                   quoteText: this.quoteText,
                   quoteAuthor: this.quoteAuthor,
                   quoteGenre: this.quoteGenre,
                   timeCreating: time
                }
                console.log(item);
                this.$emit('addquote', item)
                this.quoteText = '';
                   this.quoteAuthor = '';
                   this.quoteGenre = '';
                   this.addedId = 1;
                }
              },
              changequote(){
                let today = new Date();
                let timeChanging = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
                 if(this.name != '' && this.quoteAuthor != '' && this.quoteGenre != ''){
                   const editedquote = {
                    //    id: this.editquote.id,
                       quoteText: this.quoteText,
                       quoteAuthor: this.quoteAuthor,
                       quoteGenre: this.quoteGenre,
                       timeEditing: timeChanging,
                    //    timeCreating: this.timeCreating
                   }
                   this.$emit('editedquote', editedquote, editedquote.firebaseId)
                   this.closeModal()
                //    console.log(this.timeEditing);
                 }
              },  
           },
       }
   </script>
   
   <style>
       .modal{
           background-color: rgba(0, 0, 0, 0.35);
           position: fixed;
           top: 0;
           left: 0;
           width: 100%;
           height: 100%;
           z-index: 9999;
           display: flex;
           justify-content: center;
           align-items: center;
       }
   
       .modal__block{
           background: linear-gradient(0deg, rgba(103, 80, 164, 0.11), rgba(103, 80, 164, 0.11)), #fffbfe;
           border-radius: 28px;
           max-width: 312px;
           width: 100%;
           padding: 24px;
       }
   
       .modal__title{
           color: #1c1b1f;
           font-size: 24px;
           line-height: 32px;
           margin-bottom: 16px;
       }
   
       .modal__inputs{
           display: flex;
           flex-direction: column;
           gap: 16px;
       }
   
       .modal__inputs label{
           position: relative;
   
       }
   
       .modal__inputs span{
           position: absolute;
           left: 5px;
           top: 5px;
       }
   
       .modal__inputs input{
           background: #e7e0ec;
           border-radius: 4px;
           width: 100%;
           border: none;
           outline: none;
           padding: 23px 0 9px 16px;
           font-size: 16px;
           line-height: 24px;
           color: #49454f;
           border-bottom: 1px solid #1c1b1f;
       }
   
       .warning{
           color: red;
           font-size: 16px;
           line-height: 24px;
       }
   
       .modal__btns{
           display: flex;
           justify-content: flex-end;
           gap: 10px;
           margin-top: 25px;
   
       }
   
       .modal__btn{
           font-size: 14px;
           line-height: 20px;
           font-family: 'RM';
           background: transparent;
           padding: 10px 12px;
           text-transform: uppercase;
           border: none;
           outline: none;
       }
   
       .cancel{
           color: #cf1b1b;
       }
       .cancel:hover{
           background: #ffe1e1;
       cursor: pointer;
       }
   
       .add{
       color: #6750a4;
       }
      .add:hover{
       background: #e6ddff;
       cursor: pointer;
       }
   
       .modal-enter-active,
       .modal-leave-active{
           transition: 0.5s linear;
       }
   
       .modal-enter-from,
       .modal-leave-to{
           opacity: 0;
           transform: scale(1.5);
       }
   </style>