<script >
import { onMounted, created } from "vue";

import Navbar from "./components/Navbar.vue";
import Quotes from "./components/Quotes.vue";
import Modal from "./components/Modal.vue";
import AddButton from "./components/AddButton.vue";
import { doc, addDoc, deleteDoc, updateDoc, collection, onSnapshot, QuerySnapshot } from "@firebase/firestore";
import { db } from "./firebase";
import $store from './store'

export default {
  components: {
    Navbar,
    Quotes,
    Modal,
    AddButton
  },
  data() {
    return {
      currentId: 1,
      modalOpen: false,
      quotes: [],
      edit: false,
      editquote: {},
      search: '',
      quoteId: Number
    }
  },
  created() {    
    onSnapshot(collection(db, 'quotes'), (QuerySnapshot) => {
      const fbquotes = [];
      QuerySnapshot.forEach((doc) => {
        const quote = {
          id: doc.data().quoteId,
          quoteText: doc.data().quoteText,
          quoteAuthor: doc.data().quoteAuthor,
          quoteGenre: doc.data().quoteGenre,
          timeCreating: doc.data().timeCreating,
          timeEditing: doc.data().timeEditing,
          firebaseId: doc.id
        }
        fbquotes.push(quote)
        $store.state.arrFirebaseIds.push(quote.firebaseId)
      })

      this.quotes = fbquotes
    })
  },
  computed: {
    filterquotes() {
      return this.search ?
        this.quotes.filter(quote => {
          for (let key in quote) {
            if (String(quote[key]).toLowerCase().includes(this.search.toLowerCase())) {
              return quote;
            }
          }
        })
        : this.quotes;
    },
  },
  methods: {
    openModal() {
      this.modalOpen = true
    },
    closeModal() {
      this.modalOpen = false
      this.edit = false
    },
    async addquote(item) {
      let addedQuoteId;
      if (item.id <= this.quoteId) {
        addedQuoteId = this.quoteId++;
  
      } else {
        addedQuoteId = item.id
      }
      await addDoc(collection(db, 'quotes'), {
        quoteId: addedQuoteId,
        quoteText: item.quoteText,
        quoteAuthor: item.quoteAuthor,
        quoteGenre: item.quoteGenre,
        timeCreating: item.timeCreating
      })
      this.modalOpen = false
    },
    deletequote(id) {
      deleteDoc(doc(collection(db, 'quotes'), String(id)))     
    },
    changequote(id) {
      $store.state.firebaseId = id
      //  $store.state.firebaseId.toString()
      this.edit = this.modalOpen = true
      let pickedquote = this.quotes.find(quote => quote.id == id)
      this.editquote = pickedquote
      
    },
    async editedquote(quoteEdited, id) {
      await updateDoc(doc(collection(db, 'quotes'), $store.state.firebaseId), {
        quoteText: quoteEdited.quoteText,
        quoteAuthor: quoteEdited.quoteAuthor,
        quoteGenre: quoteEdited.quoteGenre,
        timeEditing: quoteEdited.timeEditing
      }
      )

      this.quotes.forEach(quote => {
        if (quote.id == quoteEdited.id) {
          quote.quoteText = quoteEdited.quoteText
          quote.quoteAuthor = quoteEdited.quoteAuthor
          quote.quoteGenre = quoteEdited.quoteGenre
          quote.timeEditing = quoteEdited.timeEditing
          // this.timeCreating =  quoteEdited.timeCreating
        }
      })
    },
    getquotes() {
      const localquotes = localStorage.quotes
      if (localquotes) {
        this.quotes = JSON.parse(localquotes)
      }
    },
    async random() {
      let randomIndex = Math.round(Math.random() * (this.quotes.length - 1) + 1);
      console.log(randomIndex);
      this.quotes = this.quotes.filter(quote => quote.id == randomIndex)    
    },
    async allquotes() {
      onSnapshot(collection(db, 'quotes'), (QuerySnapshot) => {
        const fbquotes = [];
        QuerySnapshot.forEach((doc) => {
          const quote = {
            id: doc.data().quoteId,
            quoteText: doc.data().quoteText,
            quoteAuthor: doc.data().quoteAuthor,
            quoteGenre: doc.data().quoteGenre,
            timeCreating: doc.data().timeCreating,
            timeEditing: doc.data().timeEditing,
            firebaseId: doc.id
          }
          fbquotes.push(quote)
          $store.state.arrFirebaseIds.push(quote.firebaseId)
        })
        this.quotes = fbquotes
      })
    }
  },
  watch: {
    quotes: {
      handler(newquotes) {
        localStorage.quotes = JSON.stringify(this.quotes)
      },
      deep: true
    }
  }
}
</script>
<template>
  <Navbar @searchValue="search = $event" />
  <Quotes :quotes="filterquotes" @delquote="deletequote" @changequote="changequote" :search="search" @random="random"
    @allquotes="allquotes" />
  <Modal :quotes="quotes" :edit="edit" :editquote="editquote" @addquote="addquote" :currentId="currentId"
    v-show="modalOpen" @closeModal="closeModal" @editedquote="editedquote" />
  <AddButton @openModal="openModal" />
</template>






