<template>
    <div class="quotes">
        <div class="container">
            <div class="quotes__top">
                <h2 class="quotes__title">{{quotes.length > 0 ? 'Все цитаты' : 'Нет цитат' }}</h2>
                <button class="quotes__btn-random" @click="$emit('random')">
                    Вывести рандомную цитату
                </button>
                <button class="quotes__btn-random" @click="$emit('allquotes')">
                    Вернуть все цитаты
                </button>
                <button class="quotes__btn" @click="grid = !grid">
                    <img src="../assets/img/list.png" alt="" v-if="grid">
                    <img src="../assets/img/grid.png" alt="" v-else> 
                    <span>{{grid ? 'Список' : 'Сетка'}}</span>
                </button>
            </div>
            <div class="quotes__list" :class="{active: !grid}">
            <QuoteItem :grid="grid" 
            v-for="quote in quotes"
            :key="quote.id"
            :quote="quote"
            @changequote="$emit('changequote', quote.firebaseId)"
            @delquote="$emit('delquote', quote.firebaseId )"
            />
            </div>
        </div>
    </div>
    
    </template>
    
    <script>
        import QuoteItem from './QuoteItem.vue';
        export default{
            components: {
                QuoteItem
            },
         data(){
            return {
                // imgList: 'src/assets/img/list.png',
                // imgGrid: 'src/assets/img/grid.png',
                grid: true
            }
         },
         props: {
            quotes: {
                typeof: Array
            }
         },
        
        }
    </script>
    
    <style>
    .quotes{
        margin-top: 30px;
        padding-bottom: 50px;
    }
    
    .quotes__top{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    .quotes__title{
        font-size: 22px;
        line-height: 28px;
        color: #323232;
    }
    
    .quotes__btn{
        background: linear-gradient(0deg, rgba(103, 80, 164, 0.11), rgba(103, 80, 164, 0.11)), #fffbfe;
        box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15), 0px 1px 3px rgba(0, 0, 0, 0.3);
        border-radius: 16px;
        cursor: pointer;
        width: 120px;
        height: 56px;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 15px;
        border: none;
        outline: none;
    }

    .quotes__btn-random{
        background: linear-gradient(0deg, rgba(103, 80, 164, 0.11), rgba(103, 80, 164, 0.11)), #fffbfe;
        box-shadow: 0px 4px 8px 3px rgba(0, 0, 0, 0.15), 0px 1px 3px rgba(0, 0, 0, 0.3);
        border-radius: 16px;
        cursor: pointer; 
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px;
        border: none;
        outline: none;
        font-size: 18px;
        line-height: 24px;
        color: #323232;
    }
    
    .quotes__btn span{
        color: #6750a4;
        font-size: 14px;
        line-height: 20px;
        font-family: 'RM';
    }
    
    .quotes__list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 24px;
        margin-top: 30px;
    }
    
    .quotes__list.active{
        grid-template-columns: 1fr;
    }

    @media (max-width: 1200px){
        .quotes__list{
            grid-template-columns: repeat(2, 1fr);
        }
    }

    @media (max-width: 790px){
        .quotes__top{
            flex-wrap: wrap;
            gap: 30px;
            width: 80%;
            margin: 0 auto;
            justify-content: space-evenly;
        }

    }

    @media (max-width: 700px) {
        .quotes__list{
            grid-template-columns: 1fr;
        }

        .quotes__item{
            text-align: center;
        }

        .quotes__btn{
            display: none;
        }
    }

    @media (max-width: 550px) {
        .quotes__btn-random{
            font-size: 16px;
        }
    }

    @media (max-width: 350px) {
        .quotes__item{
            max-width: 290px;
        }

        .quotes__item-btn{
            padding: 12px 0;
        }
    }
    </style>